<template>
  <div class="new-form">
    <el-form ref="form" label-width="80px">
      <el-form-item label="用户名:">
        <span>{{user_name}}</span>
      </el-form-item>
      <el-form-item label="教师姓名:">
        <span>{{teacher_name}}</span>
      </el-form-item>
      <el-form-item label="联系电话:">
        <span v-if="!ifEditOpen">{{teacher_tel}}</span>
        <el-input v-if="ifEditOpen" v-model="teacher_tel"></el-input>
      </el-form-item>
      <el-form-item label="固定邮箱:">
        <span v-if="!ifEditOpen">{{teacher_email}}</span>
        <el-input v-if="ifEditOpen" v-model="teacher_email"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" @click="editopen">编辑</el-button>
        <el-button type="primary" @click="saveinformation">立即保存</el-button>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>
export default {
  name: "personal-information",
  data () {
    return {
      ifEditOpen: false,
      savejson: {},
      user_name: "zlbsteve",
      teacher_name: "张立波",
      radio: "1",
      teacher_tel: 12345678900,
      teacher_email: "fxxk@fxxk.com",
      teacher_text: ""
    };
  },
  computed: {},
  methods: {
    editopen () {
      this.ifEditOpen = true;
    },
    saveinformation () {
      let information = {
        tel: this.teacher_tel,
        email: this.teacher_email
      };
      this.putRequest("user", information).then(response => {
        console.log(response);
        this.savejson.username = this.user_name;
        this.savejson.name = this.teacher_name;
        this.savejson.tel = this.teacher_tel;
        this.savejson.email = this.teacher_email;
        let savestore = JSON.stringify(this.savejson);
        localStorage.setItem("userInfo", savestore);
        this.ifEditOpen = false;
      });
    },
    clearinformation () {
      (this.teacher_tel = ""), (this.teacher_email = "");
    }
  },
  mounted () {
    let userInfoString = localStorage.getItem("userInfo");
    this.savejson = JSON.parse(userInfoString);
    this.user_name = this.savejson.username;
    this.teacher_name = this.savejson.name;
    this.teacher_tel = this.savejson.tel;
    this.teacher_email = this.savejson.email;
  }
};
</script>

<style lang="scss" scoped>
.new-form {
  width: 600px;
}
</style>


