<template>
  <div>
    <div class="div-flex">
      <span class="item" :key="index" v-for="(exerciseType, index) in exerciseTypes">
        {{exerciseType.name}}
        <i v-if="edit" type="text" size="small" class="el-icon-edit item-icon" @click="updateItem(exerciseType.id)"></i>
        <i v-if="edit" type="text" size="small" class="el-icon-close item-icon" @click="removeItem(exerciseType.id)"></i>
      </span>
      <span v-if="!edit" class="item-add" @click="edit = !edit">
        <i class="el-icon-edit"></i>
      </span>
      <span v-if="edit" class="item-add" @click="dialogAddVisible = true">添加</span>
      <span v-if="edit" class="item-add" @click="edit = false">取消</span>
    </div>

    <el-dialog title="修改试卷类型" :visible.sync="dialogUpdateVisible">
      <el-input v-model="updateInput" auto-complete="off"></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateDone" :disabled="updateInput === ''">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="添加试卷类型" :visible.sync="dialogAddVisible">
      <el-input v-model="addInput" auto-complete="off"></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addDone" :disabled="addInput === ''">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
export default {
  name: 'exercise-type',
  data () {
    return {
      edit: false,
      dialogUpdateVisible: false,
      dialogAddVisible: false,

      typeToUpdate: '',
      updateInput: '',
      addInput: ''
    }
  },
  computed: {
    exerciseTypes () {
      let exerciseTypes = this.$store.state.exerciseType.exerciseTypes
      if (exerciseTypes === null) {
        this.$store.dispatch('getExerciseTypes')
        return []
      }
      return exerciseTypes
    }
  },
  methods: {
    updateItem (id) {
      this.dialogUpdateVisible = true
      this.typeToUpdate = id
    },

    updateDone () {
      this.dialogUpdateVisible = false

      let params = { subjectId: this.getSubjectId(), exerciseType: this.typeToUpdate, exerciseTypeName: this.updateInput }
      this.putRequest(this.httpUrl.exerciseType, params).then(response => {
        this.$store.dispatch('getExerciseTypes')
        this.updateInput = ''
      })
    },

    removeItem (id) {
      this.$confirm('删除该题目类型？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(() => {
        this.deleteRequest(this.httpUrl.exerciseType, { exerciseType: id }).then(response => {
          this.$store.dispatch('getExerciseTypes')
        })
      })
    },

    addDone () {
      this.dialogAddVisible = false

      let params = { subjectId: this.getSubjectId(), exerciseTypeName: this.addInput }
      this.postRequest(this.httpUrl.exerciseType, params).then(response => {
        this.$store.dispatch('getExerciseTypes')
        this.addInput = ''
      })
    },

    getSubjectId () {
      let subjectString = localStorage.getItem('userSubject')
      let subject = JSON.parse(subjectString)

      return subject.id
    }
  }
}
</script>

<style lang="scss" scoped>
.item {
  margin-right: 10px;
  padding: 6px 12px;

  color: $color-blue;
  border: 1px solid rgba(45, 171, 226, 0.2);
  border-radius: 4px;
  background-color: rgba(45, 171, 226, 0.1);

  font-size: 14px;

  &-icon {
    margin-left: 6px;
    cursor: pointer;
  }
}

.item-add {
  margin-right: 10px;
  padding: 6px 12px;

  color: $color-blue;
  border: 1px solid rgba(45, 171, 226, 0.2);
  border-radius: 4px;

  font-size: 14px;
  cursor: pointer;
}
</style>




