<template>
  <div class="div-flex">

    <!-- 年级 -->
    <div id="textbook-book" class="textbook-grade textbook-border">
      <div class="box-head">
        <div class="label">年级</div>
      </div>
      <div class="box-main">
        <el-tree ref="treeGrade" :data="grades" node-key="id" :highlight-current="true" @node-click="selectGrade" :default-expand-all="true" :expand-on-click-node="false">
          <span slot-scope="{ node, data }">{{ node.label }}</span>
        </el-tree>
      </div>
    </div>

    <!-- 教材 -->
    <div id="textbook-book" class="textbook-book textbook-border">
      <div class="box-head">
        <div class="label">教材</div>
        <el-button class="ml" type="text" @click="clickEdit">{{!edit ? '编辑' : '取消'}}</el-button>
      </div>
      <div class="box-main">
        <el-tree ref="treeBook" :data="textbooks" node-key="id" :highlight-current="true" @node-click="selectTextbook" :default-expand-all="true" :expand-on-click-node="false">
          <span slot-scope="{ node, data }">
            <span>{{ node.label }}</span>
            <span>
              <el-button v-if="edit" type="text" icon="el-icon-edit" @click="editTextbook(node)"></el-button>
            </span>
          </span>
        </el-tree>
      </div>
    </div>

    <!-- 章节树 -->
    <div id="textbook-chapter" class="textbook-chapter textbook-border">
      <div class="box-head">
        <div class="label">章节</div>
        <el-button class="ml" type="text" @click="clickEditChapter">{{!editChapter ? '编辑' : '取消'}}</el-button>
      </div>
      <div class="box-main">
        <el-tree ref="tree" :data="chapterTree" node-key="id" :highlight-current="true" @node-click="selectChapter" :default-expand-all="true" :expand-on-click-node="false">
          <span slot-scope="{ node, data }">
            <span>{{ node.label }}</span>
            <span>
              <el-button v-if="editChapter" type="text" icon="el-icon-edit" @click="editBook(node)"></el-button>
            </span>
          </span>
        </el-tree>
      </div>
    </div>

    <!-- 章节详解 -->
    <div class="textbook-detail textbook-border">
      <div class="box-head">
        <div class="label">章节内容</div>
        <el-button class="ml" v-if="editContent" type="text" @click="updateBookContent">提交</el-button>
        <el-button class="ml" type="text" @click="clickEditContent">{{!editContent ? '编辑' : '取消'}}</el-button>
      </div>
      <div class="box-main">
        <div class="m" v-show="!editContent">{{selectedChapter.content}}</div>
        <div id="container-t" v-show="editContent" type="text/plain" style="height:80%"></div>
      </div>
    </div>

    <el-dialog title="编辑教材" :visible.sync="editBookDialogVisible">
      <span slot="footer" class="dialog-footer">
        <el-button @click="editBookMode(11)">添加教材</el-button>
        <el-button @click="editBookMode(12)">删除该教材</el-button>
        <el-button @click="editBookMode(13)">修改该教材</el-button>
        <el-button @click="editBookDialogVisible = false">取消</el-button>
      </span>
    </el-dialog>

    <el-dialog title="编辑章节" :visible.sync="editDialogVisible">
      <span slot="footer" class="dialog-footer">
        <el-button @click="editBookMode(0)">添加章节</el-button>
        <el-button @click="editBookMode(1)">添加子章节</el-button>
        <el-button @click="editBookMode(2)">删除该章节</el-button>
        <el-button @click="editBookMode(3)">修改该章节</el-button>
        <el-button @click="editDialogVisible = false">取消</el-button>
      </span>
    </el-dialog>

    <el-dialog :title="editTitles[editMode]" :visible.sync="editFormDialogVisible">
      <el-form v-if="editMode < 2">
        <el-form-item label="章节名称" label-width="90px">
          <el-input v-model="inputChapterName" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>

      <el-form v-if="editMode === 11">
        <el-form-item label="教材名称" label-width="90px">
          <el-input v-model="inputBookName" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>

      <span v-if="editMode === 2">
        确认删除章节：
        <span class="book-highlight">{{selectedChapter.label}}</span>
        <span>{{selectedChapter.children.length > 0 ? '及其子章节' : ''}}</span>
        ?
      </span>

      <span v-if="editMode === 12">
        确认删除教材：
        <span class="book-highlight">{{selectedBook.label}}</span>
        ?
      </span>

      <div v-if="editMode === 3">
        <span>将章节：
          <span class="book-highlight">{{selectedChapter.label}}</span>
        </span>
        <el-form>
          <el-form-item label="修改为" label-width="54px">
            <el-input v-model="inputChapterName" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
      </div>

      <div v-if="editMode === 13">
        <span>将教材：
          <span class="book-highlight">{{selectedBook.label}}</span>
        </span>
        <el-form>
          <el-form-item label="修改为" label-width="54px">
            <el-input v-model="inputBookName" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button @click="editBookCancle">取 消</el-button>
        <el-button type="primary" @click="editBookDone">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import "@/../static/plugins/ueditor-math-frontend/ueditor.config.js"
import "@/../static/plugins/ueditor-math-frontend/ueditor.all.min.js"
import "@/../static/plugins/ueditor-math-frontend/lang/zh-cn/zh-cn.js"
import "@/../static/plugins/ueditor-math-frontend/third-party/kityformula-plugin/addKityFormulaDialog.js"
import "@/../static/plugins/ueditor-math-frontend/third-party/kityformula-plugin/defaultFilterFix.js"
import { SET_TEXTBOOK_LIST } from '@/store/mutation-types'

export default {
  data () {
    return {
      grade: 7,

      // 当前点选的教材和章节
      selectedTextbookNumber: '',
      selectedBook: {},
      selectedChapter: {},

      // 修改教材、章节、章节内容的标志
      edit: false,
      editChapter: false,
      editContent: false,

      // 对话框可见性
      editDialogVisible: false,
      editBookDialogVisible: false,
      editFormDialogVisible: false,

      // input组件绑定的数据   
      inputBookName: '',
      inputChapterName: '',
      inputChapterContent: '',

      editMode: 0,
      editTitle: '',
      editTitles: ['添加章节', '添加子章节', '删除该章节', '修改该章节'],
      parentLength: 0,

      //题目编辑器
      instance: null
    }
  },
  computed: {
    grades () {
      let grades1 = this.$store.getters.getGrades()
      let grades2 = []
      grades1.forEach(element => {
        let obj = { id: element.id, label: element.name }
        grades2.push(obj)
      })
      return grades2
    },

    textbooks () {
      let emptyList = []
      let list = this.$store.getters.textbookList(this.grade, this.$store.getters.getSubject())
      if (list.length > 0) {
        // 统一格式
        list.forEach(element => {
          let obj = {
            grade: element.bookGrade,
            id: element.bookNumber,
            label: element.bookName,
            level: element.bookLevel,
            pnumber: element.bookPNumber,
            content: element.bookContent,
            subject: element.bookSubject
          }
          emptyList.push(obj)
        })
        this.selectedTextbookNumber = emptyList[0].id
        return emptyList
      } else {
        // 如果当前年级科目下没有教材，那么给this.textbooks一个空的教材，以便在其基础上新建教材
        return [{
          grade: this.grade,
          id: '',
          label: '',
          level: 0,
          pnumber: 'k',
          subject: this.$store.getters.getSubject()
        }]
      }
    },

    chapterTree: {
      get: function () {
        let tree = this.$store.getters.chapterTree(this.grade, this.$store.getters.getSubject(), this.selectedBook.id)
        if (tree.length > 0) {
          return tree
        } else {
          return [{
            grade: this.grade,
            id: '',
            label: '',
            level: 1,
            pnumber: this.selectedTextbookNumber,
            subject: this.$store.getters.getSubject()
          }]
        }
      },
      // 如果要对计算属性赋值，需要实现setter方法
      set: function (newValue) {
        return newValue
      }
    }
  },
  mounted () {
    this.initEditor()
    this.getTextbookList()
  },
  methods: {
    /**
     * 题目编辑器
     */
    initEditor () {
      this.$nextTick(() => {
        this.instance = UE.getEditor("container-t")
        this.instance.addListener("ready", () => {
          this.$emit("ready", this.instance)
          this.instance.setContent(this.selectedChapter.content)
        })
      })
    },

    // 获取教材/章节数据
    getTextbookList () {
      if (this.$store.state.textbook.textbookList.length === 0) {
        this.$store.dispatch('loading', true)
        this.getRequest(this.httpUrl.book, { pageSize: 1000, pageNum: 1 }).then(response => {
          this.$store.dispatch('loading', false)
          this.$store.commit(SET_TEXTBOOK_LIST, response.data.list)
          this.highLigthCurrentBook()
        }).catch(error => {
          this.$store.dispatch('loading', false)
        })
      } else {
        this.highLigthCurrentBook()
      }
    },

    // 高亮当前选中的教材章节
    highLigthCurrentBook () {
      this.$refs.treeGrade.setCurrentKey(this.grade)

      if (this.selectedBook.id === undefined) {
        // 如果当前没有选中教材，那就默认选中第一个教材
        this.selectedBook = this.textbooks[0]
        this.$refs.treeBook.setCurrentKey(this.selectedBook.id)
      }
      if (this.selectedChapter.id === undefined) {
        // 如果当前没有选中章节，那就默认选中第一个教材的第一个章节
        this.selectedChapter = this.chapterTree[0]
      }

      this.$nextTick(() => {
        this.$refs.tree.setCurrentKey(this.selectedChapter.id)
      })
    },

    // 用户点击了年级
    selectGrade (grade) {
      this.grade = grade.id
      this.selectedBook = this.textbooks[0]
      this.selectedChapter = this.chapterTree[0]
      this.inputChapterContent = this.selectedChapter.content
      this.$nextTick(() => {
        this.$refs.treeBook.setCurrentKey(this.selectedBook.id)
        this.$refs.tree.setCurrentKey(this.selectedChapter.id)
      })

      this.edit = false
      this.editChapter = false
      this.editContent = false
    },

    // 用户点击教材，将selectedTextbookNumber置为当前选中教材的id，同时匹配教材对应的章节
    selectTextbook (textbook) {
      this.selectedBook = textbook
      this.selectedChapter = this.chapterTree[0]
      this.$nextTick(() => {
        this.$refs.tree.setCurrentKey(this.selectedChapter.id)
      })
      this.edit = false
      this.editChapter = false
      this.editContent = false
    },

    // 用户点击了章节
    selectChapter (book) {
      this.selectedChapter = book
      this.inputChapterContent = this.selectedChapter.content
      this.instance.setContent(this.inputKnowledgeContent)
      this.edit = false
      this.editChapter = false
      this.editContent = false
    },

    //用户点击编辑按钮，编辑教材
    clickEdit () {
      this.edit = !this.edit
      this.editChapter = false
      this.editContent = false
    },

    //用户点击编辑按钮，编辑章节
    clickEditChapter () {
      this.editChapter = !this.editChapter
      this.edit = false
      this.editContent = false
    },

    //用户点击编辑按钮，编辑章节内容
    clickEditContent () {
      this.editContent = !this.editContent
      this.edit = false
      this.editChapter = false
    },

    // 用户选中了某教材的编辑按钮
    editTextbook (book) {
      this.editBookDialogVisible = true

      this.selectedBook = book
    },

    // 用户选中了某章节的编辑按钮，弹出对话框选择编辑类型
    editBook (book) {
      this.editDialogVisible = true

      this.selectedChapter = book.data
    },

    // 用户选择了编辑类型
    editBookMode (mode) {
      this.editBookDialogVisible = false
      this.editDialogVisible = false
      this.editFormDialogVisible = true

      this.editMode = mode
    },

    // 用户取消了编辑
    editBookCancle () {
      this.editFormDialogVisible = false
    },

    // 用户编辑完成，点击了确定按钮，此时根据editMode调用不同的编辑方法
    editBookDone () {
      this.editFormDialogVisible = false
      switch (this.editMode) {
        case 0:
          this.addBook()
          break;
        case 1:
          this.addSubBook()
          break;
        case 2:
          this.removeChapter()
          break;
        case 3:
          this.updateChapter()
          break;
        case 11:
          this.addBook()
          break;
        case 12:
          this.removeBook()
          break;
        case 13:
          this.updateBook()
          break;
        default:
          break;
      }
    },

    // 添加同级教材/章节
    addBook () {
      // 新教材
      let b = {}
      b.bookName = this.inputBookName
      b.bookGrade = this.selectedBook.grade
      b.bookLevel = 0
      b.bookPNumber = 'b'
      b.bookContent = ''
      b.bookSubject = this.selectedBook.subject

      // 计算教材个数，以便为新加教材指定number
      let parentLength = 0
      if (this.selectedBook.id !== '') {
        parentLength = this.textbooks.length
      } else {
        // this.selectedBook.id === '' 说明该教材原本是空的，this.selectedBook是一个空教材，新建教材的时候不计算它
        parentLength = 1
      }

      if (this.parentLength < 10) {
        parentLength = '0' + parentLength
      }

      // 教材的bookNumber由年级、科目、教材个数来确定
      b.bookNumber = '0' + b.bookGrade + '0' + b.bookSubject + parentLength

      this.$store.dispatch('createTextbook', b)
    },

    addChapter () {
      // 新教材/章节
      let b = {}
      b.bookName = this.inputChapterName
      b.bookGrade = this.selectedChapter.grade
      b.bookLevel = this.selectedChapter.level
      b.bookPNumber = this.selectedChapter.pnumber
      b.bookContent = '该章节下还没有内容'
      b.bookSubject = this.selectedChapter.subject

      let parentLength = 0
      // 计算选中的章节的兄弟章节个数，以便为新加章节指定number
      if (this.selectedChapter.id !== '') {
        getParentLength(this.chapterTree, this.selectedChapter.id)
        parentLength++
      } else {
        // this.selectedChapter.id === '' 说明该章节树原本是空的，this.selectedChapter是一个空章节，新建章节的时候不计算它
        parentLength = 1
      }

      if (parentLength < 10) {
        parentLength = '0' + parentLength
      }

      // 构造章节的bookNumber
      // 章节的bookNumber由父章节、新建章节兄弟章节个数来确定
      b.bookNumber = b.pnumber + parentLength


      this.$store.dispatch('createTextbook', b)
    },

    // 计算选中的章节的兄弟章节个数，以便为新加章节指定number
    getParentLength (tree, bookNumber) {
      for (let i = 0; i < tree.length; i++) {
        const element = tree[i]
        if (element.id === bookNumber) {
          this.parentLength = tree.length
        } else {
          if (element.children.length > 0) {
            this.getParentLength(element.children, bookNumber)
          }
        }
      }
    },

    // 添加选中的章节的子章节
    addSubBook () {
      let b = {}
      b.bookName = this.inputChapterName
      b.bookGrade = this.selectedChapter.grade
      b.bookLevel = this.selectedChapter.level
      b.bookPNumber = this.selectedChapter.id
      b.bookContent = '该章节下还没有内容'
      b.bookLatex = ''
      b.bookSubject = this.selectedChapter.subject
      let childCount = this.selectedChapter.children.length
      if (childCount < 9) {
        childCount = '0' + ++childCount
      }
      b.bookNumber = b.bookPNumber + childCount
      this.$store.dispatch('createTextbook', b)
    },

    // 删除选中的教材
    removeBook () {
      this.removeBookSub(this.selectedBook)
    },

    // 删除选中的章节
    removeChapter () {
      this.removeBookSub(this.selectedChapter)
    },

    // 递归地删除该章节以及该章节下面的子章节
    removeBookSub (book) {
      // 如果有子章节，先删除子章节
      if (book.children !== undefined) {
        book.children.forEach(element => {
          this.removeBookSub(element)
        })
      }

      // 再删除该章节
      let data = { bookNumber: book.id }
      this.$store.dispatch('deleteTextbook', data)
    },

    // 更新选中的教材
    updateBook () {
      let b = {}
      b.bookName = this.inputBookName
      b.bookNumber = this.selectedBook.id
      this.$store.dispatch('updateTextbook', b)
    },

    // 更新选中的章节
    updateChapter () {
      let b = {}
      b.bookName = this.inputChapterName
      b.bookNumber = this.selectedChapter.id
      this.$store.dispatch('updateTextbook', b)
    },

    // 更新选中的章节内容
    updateBookContent () {
      this.editContent = false

      let b = {}
      b.bookNumber = this.selectedChapter.id
      k.bookContent = this.instance.getContent()
      k.bookLatex = this.getLatexString()

      this.$store.dispatch('loading', true)
      this.putRequest(this.httpUrl.book, b).then(response => {
        this.$store.dispatch('loading', false)
        //TODO 这里不是响应式的
        this.selectedChapter.content = this.inputChapterContent

        //还原数据
        this.inputChapterContent = ''

        this.edit = false
        this.editChapter = false
        this.editContent = false

        // 将store中的知识点清空，重新再获取知识点
        this.$store.commit(SET_TEXTBOOK_LIST, [])
        this.getTextbookList()
      }).catch(error => {
        this.$store.dispatch('loading', false)
      })
    },

    getLatexString () {
      let latexString = ''
      let childNodes = this.instance.body.childNodes[0].childNodes
      childNodes.forEach(node => {
        if (node.nodeType === 1) {
          if (node.className === 'aurthur-math-ky') {
            let s = '$' + node.dataset.latex + '$'
            latexString += s
          }
        } else if (node.nodeType === 3) {
          latexString += node.nodeValue
        }
      })
      return latexString
    }
  },
  beforeDestroy () {
    if (this.instance !== null && !this.instance.destroy) {
      this.instance.destroy();
    }
  }
}
</script>

<style lang="scss" scoped>
.textbook-item {
  display: flex;
  align-items: center;
  justify-content: space-between;

  width: 100%;
  height: 40px;
  padding: 4px;

  cursor: pointer;

  &:hover {
    background: #f8f8f8;
  }

  &-selected {
    background: #f4f4f4;

    font-size: 16px;
  }
}

.textbook-grade {
  position: absolute;
  top: 0;
  bottom: 0;

  width: 90px;
}

.textbook-book {
  position: absolute;
  left: 100px;
  top: 0;
  bottom: 0;

  width: 190px;
}

.textbook-chapter {
  position: absolute;
  left: 300px;
  top: 0;
  bottom: 0;

  width: 290px;
}

.textbook-detail {
  position: absolute;
  left: 600px;
  top: 0;
  bottom: 0;
  right: 0;
}

.textbook-border {
  border: $border-solid;
  border-radius: 4px;
}

.box {
  position: relative;
  height: 100%;
}

.box-head {
  position: absolute;
  width: 100%;
  height: 40px;
  padding: 0 10px;
  display: flex;
  align-items: center;

  background-color: $color-gray;
}

.box-main {
  width: 100%;
  top: 40px;
  bottom: 0;
  position: absolute;
  overflow-y: auto;
}

.label {
  flex: 1;
}

.textbook-highlight {
  font-weight: bold;
}

.tree-node-label {
  width: 200px;
}
</style>




