<template>
  <!-- 教师管理页面 -->
  <div class="bqteam-main-wrap-inner">
    <!-- 添加用户 -->
    <el-button class="mb" icon="el-icon-plus" @click="addUserDialogVisible = true">添加用户</el-button>

    <!-- 用户列表 -->
    <el-table :data="userArray" border v-loading="loading">
      <el-table-column prop="username" label="用户账号" width="120" align="center">
      </el-table-column>
      <el-table-column prop="name" label="用户姓名" width="120" align="center">
      </el-table-column>
      <el-table-column prop="roleName" label="用户类型" width="120" align="center">
      </el-table-column>
      <el-table-column prop="tel" label="用户手机" width="120" align="center">
      </el-table-column>
      <el-table-column prop="email" label="用户邮箱" align="center">
      </el-table-column>
      <el-table-column prop="statusString" label="用户状态" width="120" align="center">
      </el-table-column>
      <el-table-column label="操作" width="160">
        <template slot-scope="scope" fixed="right" label="操作">
          <button class="table-button" @click="editType(scope.$index)" type="text">修改类型</button>
          <button class="table-button" @click="disableUser(scope.$index)" type="text">{{scope.row.operateString}}</button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分页 -->
    <div class="mt">
      <el-pagination @current-change="pageChanged" :current-page="pageNum" layout="total, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <div>
      <!-- 新增用户弹框 -->
      <el-dialog title="添加用户" :visible.sync="addUserDialogVisible">
        <el-form :model="addUserForm" :rules="addUserFormRules" ref="addUserForm" label-width="80px">
          <el-form-item label="账号" prop="username">
            <el-input v-model="addUserForm.username"></el-input>
          </el-form-item>
          <el-form-item label="姓名" prop="name">
            <el-input v-model="addUserForm.name"></el-input>
          </el-form-item>
          <el-form-item label="身份证号" prop="idNumber">
            <el-input v-model="addUserForm.idNumber"></el-input>
          </el-form-item>
          <el-form-item label="电话" prop="tel">
            <el-input v-model="addUserForm.tel"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="addUserForm.email"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addUserDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="addUser">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 修改用户类型弹框 -->
      <el-dialog title="修改用户类型" :visible.sync="editRoleTypeDialogVisible">
        <el-radio-group v-model="roleId">
          <el-radio v-for="role in roleArray" :key="role.roleId" :label="role.roleId">{{role.roleName}}</el-radio>
        </el-radio-group>

        <div slot="footer" class="dialog-footer">
          <el-button @click="editRoleTypeDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="editTypeConfirm">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      userArray: [],
      roleArray: [],

      pageNum: 1,
      total: 0,

      addUserDialogVisible: false,
      addUserForm: {
        username: '',
        name: '',
        idNumber: '',
        tel: '',
        email: '',
        type: 1
      },
      addUserFormRules: {
        username: [
          { required: true, message: '请指定用户名', trigger: 'blur' },
          { min: 4, max: 18, message: '长度在 4 到 18 个字符', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入用户姓名', trigger: 'blur' },
          { min: 2, max: 10, message: '长度在 2 到 10 个字', trigger: 'blur' }
        ],
        idNumber: [
          { required: true, message: '实名制需要，请输入用户身份证号', trigger: 'blur' },
          { type: 'string', len: 18, message: '请输入18位身份证号', trigger: 'blur' }
        ],
        tel: [
          { required: false, message: '', trigger: 'blur' },
          { type: 'string', len: 11, message: '请输入11位手机号', trigger: 'blur' }
        ],
        email: [
          { required: false, message: '请输入活动名称', trigger: 'blur' },
          { type: 'string', min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ]
      },

      editRoleTypeDialogVisible: false,
      editRoleTypeIndex: 0,
      roleId: 0,

      loading: false,
    };
  },
  mounted () {
    this.getUsers()
    this.getRoles()
  },
  methods: {
    getUsers () {
      this.loading = true
      this.getRequest(this.httpUrl.sysUser, { pageSize: 10, pageNum: this.pageNum })
        .then(response => {
          let statusStrings = ['正常', '锁定', '休眠', '已删除']
          let operateStrings = ['锁定', '解锁', '休眠', '已删除']
          this.loading = false
          this.userArray = []
          response.data.list.forEach(element => {
            element.statusString = statusStrings[element.status]
            element.operateString = operateStrings[element.status]
            if (element.status !== 3) {
              this.userArray.push(element)
            }
          })
          this.total = this.userArray.length
        })
        .catch(error => {
          this.loading = false
        })
    },

    getRoles () {
      this.getRequest(this.httpUrl.sysRole, {})
        .then(response => {
          this.roleArray = response.data.list
        })
        .catch(error => {
        })
    },

    addUser () {
      this.$refs.addUserForm.validate((valid) => {
        if (valid) {
          this.postRequest(this.httpUrl.sysUser, this.addUserForm)
            .then(response => {
              this.addUserDialogVisible = false
              this.getUsers()
            })
            .catch(error => {
            })
        } else {
          return false;
        }
      })
    },

    editType (index) {
      this.roleId = this.userArray[index].roleId
      this.editRoleTypeIndex = index
      this.editRoleTypeDialogVisible = true
    },

    editTypeConfirm () {
      this.editRoleTypeDialogVisible = false

      let parmas = { username: this.userArray[this.editRoleTypeIndex].username, roleId: this.roleId }
      this.putRequest(this.httpUrl.sysUserRole, parmas)
        .then(response => {
          this.getUsers()
        })
        .catch(error => {
        })
    },

    disableUser (index) {
      let user = this.userArray[index]
      user.lock = user.status === 0 ? 1 : 0
      this.putRequest(this.httpUrl.sysUser, user)
        .then(response => {
          this.getUsers()
        })
        .catch(error => {
        })
    },

    pageChanged (page) {
      this.pageNum = page
      this.getUsers()
    }
  }
};
</script>

<style lang="scss" scoped>
.el-input {
  width: 300px;
}
</style>




