        {
          IP: "192.168.1.3",
          logintime: "2018-07-24 13:53",
          eventtype:"type",
          eventdescription:"description",
          logtype:"type",
          result:"success",
          level:"1"
        },
        {
          IP: "192.168.1.3",
          logintime: "2018-07-24 13:53",
          eventtype:"type",
          eventdescription:"description",
          logtype:"type",
          result:"success",
          level:"1"
        }
      ]
    };
  },
  methods: {
    search() {}
  }
};
</script>

<style>
</style>



