import { SET_TEXTBOOK_LIST } from '../mutation-types'
//  4个请求方法以及url
import {
  getRequest,
  postRequest,
  putRequest,
  deleteRequest,
  httpUrl
} from 'utils/fetch.js'

const state = {
  // 教材原始数据
  textbookList: [],
  // 教材列表
  textbooks: []
}

const getters = {
  // 对应年级科目的教材列表
  textbookList: state => (grade, subject) => {
    // 拿出对应年级科目下的章节
    let list = []
    state.textbookList.forEach(textbook => {
      if (textbook.bookGrade === grade && textbook.bookSubject === subject && textbook.bookLevel === 0) {
        list.push(textbook)
      }
    })
    return list
  },

  // 章节树
  chapterTree: state => (grade, subject, textbookNumber) => {
    // 拿出对应年级科目下的章节
    let list = []
    state.textbookList.forEach(textbook => {
      if (textbook.bookGrade === grade && textbook.bookSubject === subject && textbook.bookLevel > 0) {
        list.push(textbook)
      }
    })

    // 知识点树型结构
    let tree = formChapterTree(list, textbookNumber)
    return tree
  },

  chapterTreeWithoutChildren: state => (grade, subject) => {
    // 拿出对应年级科目下的章节
    let list = []
    state.textbookList.forEach(textbook => {
      if (textbook.bookGrade === grade && textbook.bookSubject === subject) {
        list.push(textbook)
      }
    })

    // 知识点树型结构
    let tree = formChapterTreeWithoutChildren(list, 'b')
    return tree
  },

  // 根据节number拿到节名
  sectionName: state => (sectionNumber) => {
    for (let i = 0; i < state.textbookList.length; i++) {
      if (state.textbookList[i].bookNumber === sectionNumber) {
        return state.textbookList[i].bookName
      }
    }
    return ''
  }
}

function formChapterTree (list, pnumber) {
  // 章节树
  let tree = []

  list.forEach(textbook => {
    if (textbook.bookPNumber === pnumber) {
      let obj = {
        id: textbook.bookNumber,
        label: textbook.bookName,
        grade: textbook.bookGrade,
        subject: textbook.bookSubject,
        level: textbook.bookLevel,
        pnumber: textbook.bookPNumber,
        content: textbook.bookContent,
        children: formChapterTree(list, textbook.bookNumber)
      }
      tree.push(obj)
    }
  })
  return tree
}

function formChapterTreeWithoutChildren (list, pnumber) {
  // 章节树
  let tree = []

  list.forEach(textbook => {
    if (textbook.bookPNumber === pnumber) {
      let obj = {
        value: textbook.bookNumber,
        label: textbook.bookName,
        children: formChapterTreeWithoutChildren(list, textbook.bookNumber)
      }
      if (obj.children.length === 0) {
        let t = { value: obj.value, label: obj.label }
        obj = t
      }
      tree.push(obj)
    }
  })
  return tree
}

const mutations = {
  [SET_TEXTBOOK_LIST]: (state, textbookList) => {
    state.textbookList = textbookList
    state.textbooks.lenght = 0
    textbookList.forEach(textbook => {
      if (textbook.bookLevel === 0) {
        state.textbooks.push(textbook)
      }
    })
  }
}

const actions = {
  getTextbook (context) {
    if (context.state.textbookList.length === 0) {
      getRequest(httpUrl.book, { pageSize: 1000, pageNum: 1 }).then(response => {
        context.commit(SET_TEXTBOOK_LIST, response.data.list)
      })
      .catch(error => {
      })
    }
  },

  createTextbook (context, params) {
    postRequest(httpUrl.book, params).then(response => {
      context.dispatch('editTextbookSucceed')
    })
    .catch(error => {
    })
  },

  updateTextbook (context, params) {
    putRequest(httpUrl.book, params).then(response => {
      context.dispatch('editTextbookSucceed')
    })
    .catch(error => {
    })
  },

  deleteTextbook (context, params) {
    deleteRequest(httpUrl.book, params).then(response => {
      context.dispatch('editTextbookSucceed')
    })
    .catch(error => {
    })
  },

  editTextbookSucceed (context) {
    getRequest(httpUrl.book, { pageSize: 1000, pageNum: 1 }).then(response => {
      context.commit(SET_TEXTBOOK_LIST, response.data.list)
    })
    .catch(error => {
    })
  }
}

const textbook = {
  state,
  getters,
  mutations,
  actions
}

export default textbook



